import { createRouter, createWebHashHistory } from 'vue-router'
const routes = [
  {
    path: '/login',
    name: 'login',
    component: () => import('../views/login/MyIndex.vue')
  },
  {
    path: '/',
    name: '/',
    component: () => import('@/layout/MyLayout.vue'),
    redirect: '/home',
    children: [
      {
        path: 'home',
        name: 'home',
        component: () => import('@/views/reports/MyIndex.vue')
      },
      {
        path: 'users',
        name: 'users',
        component: () => import('@/views/users/MyIndex.vue')
      },
      {
        path: 'categories',
        name: 'categories',
        component: () => import('@/views/categories/MyIndex.vue')
      },
      {
        path: 'goods',
        name: 'goods',
        component: () => import('@/views/goods/MyIndex.vue')
      },
      {
        path: 'orders',
        name: 'orders',
        component: () => import('@/views/orders/MyIndex.vue')
      },
      {
        path: 'params',
        name: 'params',
        component: () => import('@/views/params/MyIndex.vue')
      },
      {
        path: 'reports',
        name: 'reports',
        component: () => import('@/views/reports/MyIndex.vue')
      },
      {
        path: 'rights',
        name: 'rights',
        component: () => import('@/views/rights/MyIndex.vue')
      },
      {
        path: 'roles',
        name: 'roles',
        component: () => import('@/views/roles/MyIndex.vue')
      }
    ]
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
